footer {
  position: relative;
  padding: 0.26667rem;
  color: #ccc;
  text-align: center;
  font-size: 0.32rem;
}

footer::before {
  position: absolute;
  margin: 0 auto;
  top: 50%;
  left: 50%;
  display: block;
  content: "";
  width: 5.33333rem;
  height: 0.02667rem;
  background: #ddd;
  overflow: hidden;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: -1;
}

footer>span {
  padding: 0 0.26667rem;
  background: #fff;
  z-index: 2;
}

.container {
  padding-right: 0.4rem;
  padding-left: 0.4rem;
  margin-right: auto;
  margin-left: auto;
}

@supports not ((-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px))) {
  .blur {
    filter: url(blur.svg#blur);
    /* FireFox, Chrome, Opera */
    -webkit-filter: blur(0.53333rem);
    /* Chrome, Opera */
    -moz-filter: blur(0.53333rem);
    -ms-filter: blur(0.53333rem);
    filter: blur(0.53333rem);
  }
  .mf-tab-wrap {
    background-color: #fff;
    border-bottom: 0.02667rem solid #efefef;
  }
  .van-overlay {
    background-color: rgba(255, 255, 255, .82);
  }
}

@supports ((-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px))) {
  .mf-tab-wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(248, 249, 251, .8);
    -webkit-backdrop-filter: saturate(180%) blur(0.53333rem);
    backdrop-filter: saturate(180%) blur(0.53333rem);
  }
  .van-overlay {
    background-color: rgba(248, 248, 248, .82);
    -webkit-backdrop-filter: saturate(180%) blur(0.53333rem);
    backdrop-filter: saturate(180%) blur(0.53333rem);
  }
}

/* Movie Card */

.movie-card {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 1.06667rem;
  max-width: 4.21333rem;
}

.movie-card .movie-cover {
  margin-bottom: 0.26667rem;
  width: 100%;
  min-height: 5.33333rem;
  line-height: 0;
  border-radius: 0.21333rem;
  box-shadow: 0 0.16rem 0.37333rem 0 rgba(0, 0, 0, 0.22);
  overflow: hidden;
}

.movie-cover img {
  width: 100%;
}

.movie-card .title {
  padding: 0;
  margin: 0 0.10667rem 0 0;
  max-width: 6em;
  font-size: 0.48rem;
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.movie-card .score {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  line-height: 0.74667rem;
  padding-left: 0.53333rem;
  -webkit-box-align: inherit;
  -webkit-align-items: inherit;
          align-items: inherit;
  color: #ffd21e;
  font-size: 0.42667rem;
  font-weight: 500;
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 19 18' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.75 14.577l-5.652 2.971 1.079-6.293-4.572-4.457 6.319-.918 2.826-5.726 2.826 5.726 6.319.918-4.572 4.457 1.079 6.293z' fill='%23ffd21e'/%3E%3C/svg%3E")
    no-repeat left center;
  background-size: 0.48rem;
}

.movie-card .score span {
  display: inline-block;
  vertical-align: middle;
}

.movie-card a {
  display: block;
  line-height: 0;
}


