footer {
  position: relative;
  padding: 10px;
  color: #ccc;
  text-align: center;
  font-size: 12px;
}

footer::before {
  position: absolute;
  margin: 0 auto;
  top: 50%;
  left: 50%;
  display: block;
  content: "";
  width: 200px;
  height: 1px;
  background: #ddd;
  overflow: hidden;
  transform: scaleY(0.5);
  transform: translate(-50%, -50%);
  z-index: -1;
}

footer>span {
  padding: 0 10px;
  background: #fff;
  z-index: 2;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@supports not ((-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px))) {
  .blur {
    filter: url(blur.svg#blur);
    /* FireFox, Chrome, Opera */
    -webkit-filter: blur(20px);
    /* Chrome, Opera */
    -moz-filter: blur(20px);
    -ms-filter: blur(20px);
    filter: blur(20px);
  }
  .mf-tab-wrap {
    background-color: #fff;
    border-bottom: 1px solid #efefef;
  }
  .van-overlay {
    background-color: rgba(255, 255, 255, .82);
  }
}

@supports (-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px)) {
  .mf-tab-wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(248, 249, 251, .8);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
  .van-overlay {
    background-color: rgba(248, 248, 248, .82);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
}

/* Movie Card */
.movie-card {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 40px;
  max-width: 158px;
}

.movie-card .movie-cover {
  margin-bottom: 10px;
  width: 100%;
  min-height: 200px;
  line-height: 0;
  border-radius: 8px;
  box-shadow: 0 6px 14px 0 rgba(0, 0, 0, 0.22);
  overflow: hidden;
}

.movie-cover img {
  width: 100%;
}

.movie-card .title {
  padding: 0;
  margin: 0 4px 0 0;
  max-width: 6em;
  font-size: 18px;
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.movie-card .score {
  display: flex;
  line-height: 28px;
  padding-left: 20px;
  align-items: inherit;
  color: #ffd21e;
  font-size: 16px;
  font-weight: 500;
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 19 18' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.75 14.577l-5.652 2.971 1.079-6.293-4.572-4.457 6.319-.918 2.826-5.726 2.826 5.726 6.319.918-4.572 4.457 1.079 6.293z' fill='%23ffd21e'/%3E%3C/svg%3E")
    no-repeat left center;
  background-size: 18px;
}
.movie-card .score span {
  display: inline-block;
  vertical-align: middle;
}

.movie-card a {
  display: block;
  line-height: 0;
}


